	module.exports = {"data":{"markdownRemark":{"html":"<p>Gần đây, khi tìm mua loại nước cân bằng da mới (toner), mình đã tìm lotion hạt dĩ ý (chính là hạt bobo đấy ạ) của Nhật <strong>Hatomugi Skin Lotion</strong> (ở Nhật gọi là lotion dưỡng da thay cho toner). Em này làm mình ngạc nhiên hết sức vì thể tích khủng mà giá rất lại rất rẻ 500ml/8.500krw. Các blogger ca ngợi em này nhiều lắm, bảo là Hatomugi là một trong những loại toner tốt nhất và được hầu hết các phụ nữ Nhật Bản yêu thích.</p>\n<figure style=\"width: 400px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/hato-1-9c6041e94d2939d80ef038c87c2a5769-f594b.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 575px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 173.7391304347826%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"hato 1\" title=\"\" src=\"/static/hato-1-9c6041e94d2939d80ef038c87c2a5769-f594b.png\" srcset=\"/static/hato-1-9c6041e94d2939d80ef038c87c2a5769-47e4d.png 300w,\n/static/hato-1-9c6041e94d2939d80ef038c87c2a5769-f594b.png 575w\" sizes=\"(max-width: 575px) 100vw, 575px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<h2 id=\"bao-bì\"><a href=\"#bao-b%C3%AC\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Bao bì</h2>\n<p>Một chai nhựa trong suốt đơn giản có chứa toner bên trong. Toner màu trắng đục, gần như không mùi. Nắp chai cũng giống các loại toner khác, dễ dàng lấy ra.</p>\n<h2 id=\"thành-phần\"><a href=\"#th%C3%A0nh-ph%E1%BA%A7n\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Thành phần</h2>\n<p><span style=\"color:royalblue\"> Water, Butylene Glycol, Dipotassium Glycyrrhizate, Coix Lacryma Jobi Mayuen Seed Extract, Styrene/Acrylates Copolymer, Dipropylene Glycol, Alcohol, Acid Citric, Sodium Citrate, Methylparaben, Propylparaben. </span></p>\n<h2 id=\"giới-thiệu-về-hatomugi-skin-lotion\"><a href=\"#gi%E1%BB%9Bi-thi%E1%BB%87u-v%E1%BB%81-hatomugi-skin-lotion\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Giới thiệu về Hatomugi skin lotion</h2>\n<p>Thành phần chính của Hatomugi skin lotion là chiết xuất tự nhiên. Hatomugi có nghĩa là hạt dĩ ý ở Nhật (tên khoa học là Coix lacryma-jobi) có tác dụng giữ ẩm, làm trắng, chống viêm và chống lão hóa.</p>\n<ul>\n<li>Tăng cường khả năng thẩm thấu của da, làm ẩm, làm dịu làn da.</li>\n<li>Nhắm đến các vấn đề về da, chẳng hạn như lỗ chân lông to, da có nếp nhăn, và da lão hóa.</li>\n<li>Chiết xuất hạt dĩ ý giàu Vitamin B1 và B2 giúp làm sạch mụn trứng cá và cải thiện màu da không đều.\nCân bằng độ axit của da, tăng cường độ đàn hồi của da và làm ẩm da suốt cả ngày.</li>\n</ul>\n<h2 id=\"cách-dùng\"><a href=\"#c%C3%A1ch-d%C3%B9ng\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Cách dùng</h2>\n<p>Có rất nhiều cách để sử dụng skin lotion này:</p>\n<ul>\n<li><strong>Toner</strong>: Sau khi rửa mặt sạch, cho nước cân bằng ra tay hoặc bông cotton rồi nhẹ nhàng thoa lên mặt.</li>\n<li><strong>Mặt nạ dưỡng da</strong>: thấm ướt 4 miếng bông cotton và đặt lên vùng trán, 2 bên má và cằm để trong 5-10 phút. Bạn cũng có thể thay thế nó bằng một mặt nạ nén chuyên dụng.</li>\n<li><strong>Sữa dưỡng thể</strong>: Sau khi tắm thoa skin lotion này lên người.</li>\n<li><strong>Xịt khoáng</strong>: đổ skin lotion này vào một chai xịt để sử dụng bất cứ khi nào da mặt bạn cảm thấy khô.</li>\n<li>Sử dụng để <strong>làm dịu vết cháy nắng</strong>.</li>\n<li>Để tăng cường tác dụng làm mát da, Hatomugi Skin Lotion <strong>có thể được cất trong tủ lạnh</strong>.</li>\n</ul>\n<figure style=\"width: 400px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/hato-2-dc386784a2467344aaeae10abc510ad7-837b2.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 588px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 102.04081632653062%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"hato 2\" title=\"\" src=\"/static/hato-2-dc386784a2467344aaeae10abc510ad7-837b2.png\" srcset=\"/static/hato-2-dc386784a2467344aaeae10abc510ad7-1b53c.png 300w,\n/static/hato-2-dc386784a2467344aaeae10abc510ad7-837b2.png 588w\" sizes=\"(max-width: 588px) 100vw, 588px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p>Mình đã thử dùng Hatomugi skin lotion như một mặt nạ dưỡng da vì chai bự quá, xài cho mau hết. 😆  Sau khi đắp xong, mình hoàn toàn thất vọng với em này, da mình bị đỏ lên và nhìn kinh lắm, nhất là hai bên má, cũng hơi bị rát nữa, chắc là do nó có <strong>chứa cồn</strong>. Còn dùng như toner, em này cũng không có gì đặc biệt, là một toner hoàn toàn bình thường. Em này chỉ được cái chai bự và giá rẻ thôi. Mình sẽ không mua lại em này đâu. Mình nghĩ bạn nào da hỗn hợp thiên khô, da khô, da nhạy cảm dễ bị kích ứng thì không nên dùng em này.</p>","timeToRead":3,"excerpt":"Gần đây, khi tìm mua loại nước cân bằng da mới (toner), mình đã tìm lotion hạt dĩ ý (chính là hạt bobo đấy ạ) của Nhật  Hatomugi Skin Lotion…","frontmatter":{"title":"Review - Nước cân bằng da Hatomugi Skin Lotion","cover":"/images/hato.png","date":"2017/05/15","category":"Beauty","tags":["Beauty","japanese-skin-care","hatomugi-skin-lotion","skin-care-routine","nuoc-can-bang-hatomugi","my-pham","duong-da","toner"]},"fields":{"slug":"/vi/Review-Hatomugi-Skin-Lotion/"}}},"pathContext":{"path":"/vi/Review-Hatomugi-Skin-Lotion/","langKey":"vi"}}

